<template lang="pug">

  section(id="about")
    .wrapper
      .text
        h2(v-html="data.title")
        p(v-html="data.subtitle")

</template>

<script>
import axios from 'axios'
import { endpointHost } from '../config'

export default {
  name: "slide-description",

  data () {
      return {
          data: {
              title: '',
              subtitle: ''
          }
      }
  },

  created () {
    this.getDescriptionData()
      .then(r => this.data = r.data)
  },

  methods: {
    async getDescriptionData () {
      return await axios.get(`${endpointHost}api/description`)
    }
  }
}
</script>


