<template lang="pug">

  section(id="slider")
    .wrapper
      .text
        h2(v-html="slide.title")
        p(v-html="slide.subtitle")

        .arrows
          button.prev(@click="prev")
          button.next(@click="prev")

        .dots
          .dot(
            ref="dots", 
            v-for="(dot, index) in data.length", 
            v-bind:key="index", 
            @click.prevent="clickDot($event.target, index)", 
            :class="{'is-active': index === 0}"
          )

      div.image
        l-map(v-if="slide.zoom", :zoom="slide.zoom", :center="slide.center")
          l-tile-layer(:url="slide.url", :attribution="slide.attribution")
          l-marker(v-for="(marker, index) in slide.markers", v-bind:key="index" :lat-lng="marker")
            l-popup(:content="index")
        img(v-else, :src="slide.image", :alt="slide.image")
              
</template>

<script>
import 'leaflet'
import axios from 'axios'
import { LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet'
import { endpointHost } from '../config'

export default {
  name: "slide-switcher",

  data () {
    return {
      current: 0,
      timer: null,
      data: [
        {
          title: '',
          subtitle: '',
          zoom: false,
          center: [],
          markers: {},
          url: '',
          attribution: '',
          image: ''
        }
      ]
    }
  },

  computed: {
    slide () {
      return this.data[Math.abs(this.current) % this.data.length];
    }
  },

  created () {
    this.getSlidesData()
      .then(r => this.data = r.data)
  },

  methods: {
    async getSlidesData () {
      return await axios.get(`${endpointHost}api/slides`)
    },

    startRotation () {
      this.timer = setInterval(this.next, 3000)
    },

    stopRotation () {
      clearTimeout(this.timer);
      this.timer = null
    },

    clickDot (target, index) {
      const dots = this.$refs.dots

      Object.values(dots).map(dot => {
        if (dot.classList.contains('is-active')) {
          dot.classList.remove('is-active')
        }
      })

      dots[index].classList.add('is-active')
      this.current = index
    },

    next () {
      this.current += 1
    },

    prev () {
      this.current -= 1
    }
  },

  components: {
    LTileLayer,
    LMarker,
    LPopup,
    LMap
  }
}
</script>

<style>
@import "https://unpkg.com/leaflet@1.3.1/dist/leaflet.css";
</style>



