<template lang="pug">

    section(id="contact")
        .wrapper
            .text
                div
                    h2 {{ data.title }}
                    p {{ data.subtitle }}

                form(v-if="!message", @submit.prevent="validateBeforeSubmit", ref="form", novalidate)
                    label.label(for="name") Imię i Nazwisko
                    input(
                        id="name"
                        type="text",
                        name="name",
                        v-model="name",
                        v-validate="'required:true'",
                        :class="{'has-error': errors.has('name')}"
                    )

                    label.label(for="email") Adres e-mail
                    input(
                        id="email"
                        type="text",
                        name="email",
                        v-model="email",
                        v-validate="'required:true|email'",
                        :class="{'has-error': errors.has('email')}"
                    )
                    
                    label.label(for="people") Liczba osób
                    select(
                        id="people"
                        name="people",
                        v-model="people",
                        v-validate="'required:true|min:1|max:4'",
                        :class="{'has-error': errors.has('people')}"
                    )
                        option(value="1") 1
                        option(value="2") 2
                        option(value="3") 3
                        option(value="4") 4

                    input(
                        id="hotel",
                        type="checkbox",
                        name="hotel",
                        v-model="hotel"
                    )
                    label.checkbox(for="hotel")
                        span Noc w hotelu?

                    input.submit(id="submit", type="submit" value="Wyślij")
                    label.submit(for="submit")
                        span Wyślij
                
                div.message(v-else) {{ message }}

            div.image
                img(src="http://wrzesniowyslub.pl/images/contact.jpg", alt="")

</template>

<script>
import axios from 'axios'
import { endpointHost } from '../config'

export default {
    name: "slide-contact",

    inject: ['$validator'],

    data () {
        return {
            name: null,
            people: null,
            hotel: null,
            email: null,
            message: false,
            data: {
                title: '',
                subtitle: ''
            }
        }
    },

    created () {
        this.getContactData()
            .then(r => this.data = r.data)
    },

    methods: {
        validateBeforeSubmit () {
            this.$validator.validateAll()
                .then((result) => {
                    if (result) {
                        // this.$refs.form.submit()
                        this.submitForm()
                            .then(() => {
                                this.message = 'Formularz wysłany poprawnie'
                            })

                    }
                })
        },

        async getContactData () {
            return await axios.get(`${endpointHost}api/contact`)
        },

        submitForm () {
            const data = new FormData()

            data.append('name', this.name)
            data.append('people', this.people)
            data.append('hotel', this.hotel)
            data.append('email', this.email)

            return axios({
                url: `${endpointHost}api/book`,
                method: 'post',
                headers: { 
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                data: data
            })
        }
    }
};
</script>


