<template lang="pug">

  section(id="header")        
    .wrapper
      img(src="http://wrzesniowyslub.pl/images/header.jpg")
      .text
        h1(v-html="data.title")
        p(v-html="data.subtitle")

</template>

<script>
import axios from 'axios'
import { endpointHost } from '../config'

export default {
  name: 'slide-header',

  data () {
    return {
      data: {
        title: '',
        subtitle: ''
      }
    }
  },

  created () {
    this.getHeaderData()
      .then(r => this.data = r.data)
  },

  methods: {
    async getHeaderData () {
      return await axios.get(`${endpointHost}api/header`)
    }
  }
}
</script>



