import Vue from 'vue'
import App from './App.vue'
import pl from 'vee-validate/dist/locale/pl'
import VeeValidate, { Validator } from 'vee-validate'

Vue.config.productionTip = true

Validator.localize('pl', pl)
Vue.use(VeeValidate, {
    aria: false
})

new Vue({
  render: h => h(App)
}).$mount('#app')



